:root {
  --dark-color: #22252a;
  --grey-color: #272b2e;
  --grey2-color: #585859;
  --white-color: #fcfdfe;
  --red-color: #b13836;
  --turquoise-color: #5fc1ae;
  --gold-color: #f6c87b;
  --purple-color: #8c7bed;
}

* {
  margin: 0;
}
body {
  background-color: #f5f6fa;
  font-family: "Cairo", sans-serif;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px grey;
  border-radius: 2px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--grey-color);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--dark-color);
}
